import State from 'content_script_core/state'
import { IMPORT_BUTTON_STATUSES, POPUP_DURATION } from 'const'
import { importProduct } from './import'
import { updateProductButton } from './utils'

export default class AliexpressState extends State {
  constructor () {
    super()
    this.state.imported = []
    this.state.failed = []
    this.state.previouslyImported = []
  }

  moveFromImportedToPreviouslyImported (productId) {
    this.state.imported = this.state.imported.filter(id => String(id) !== String(productId))
    this.state.previouslyImported.push(productId)
    updateProductButton(productId, IMPORT_BUTTON_STATUSES.PREVIOUSLY_IMPORTED, true)
  };

  authSuccess ({ username, stores }) {
    super.authSuccess({ username })
    if (stores && stores.aliexpress) this.state.previouslyImported.push(...stores.aliexpress)
  }

  importStarted (productId) {
    super.importStarted(productId)
    importProduct(productId)
  }

  importFinished (productId) {
    super.importFinished(productId)
    this.state.imported.push(productId)
    this.state.failed = this.state.failed.filter(id => String(id) !== String(productId))
    updateProductButton(productId, IMPORT_BUTTON_STATUSES.IMPORTED, true)

    setTimeout(() => this.moveFromImportedToPreviouslyImported(productId), POPUP_DURATION)
  }

  importFailed ({ id, error }) {
    super.importFailed({ id, error })
    this.state.failed.push(id)
    updateProductButton(id, IMPORT_BUTTON_STATUSES.ERROR)
  }
}
