import setupImagesCss from './images-css'
import blockerSetup from './unload_blocker'
import setupAuth from './auth'
import { setupAnalytics } from 'analytics'

export default function setupContentScriptCommon (storeName) {
  setupImagesCss()
  blockerSetup()
  setupAnalytics(storeName.toUpperCase())
  // Set up as later so event handlers can bind
  setTimeout(setupAuth, 0)
}
