/*
 * Proxy between background job and content script. Publishes AUTH_SUCCESS and AUTH_FAILURE events.
 */
import { AUTH_SUCCESS, AUTH_FAILURE, BACKGROUND_EVENTS } from 'const'
import { publish } from 'utils'

function authenticationSuccess (data) {
  publish(AUTH_SUCCESS, data)
}

function authenticationFailure () {
  publish(AUTH_FAILURE)
}

export default function setupAuth () {
  chrome.runtime.onMessage.addListener(request => {
    if (request.topic !== BACKGROUND_EVENTS.AUTHENTICATED) return
    request.data.authenticated ? authenticationSuccess(request.data) : authenticationFailure()
  })

  chrome.runtime.sendMessage({ topic: BACKGROUND_EVENTS.AUTHENTICATE })
}
