/*
 * Adds dynamic stylesheet with extension's images
 */
import imagesList from 'images-list.json'
import $ from 'jquery'

export default function setupImagesCss () {
  let css = ''
  for (const path of imagesList) {
    const urlPath = chrome.runtime.getURL(`img/${path}`)
    const name = path.replace(/\..*$/, '')
    css += `.modalyst-extension-popup .background-img-${name} { background-image: url(${urlPath}); }`
    css += `.modalyst-image.background-img-${name} { background-image: url(${urlPath}); }`
  }

  $('head').append(`<style>${css}</style>`)
}
