/*
 * Extension popup. Displays information based on current state.
 */
import $ from 'jquery'
import debounce from 'lodash/debounce'

import defaultPopupTemplate from 'templates/popup/default-popup.hbs'

import updatingCartTemplate from 'templates/popup/checkout/init.hbs'
import updatingShippingTemplate from 'templates/popup/checkout/shipping.hbs'
import updateShippingTemplate from 'templates/popup/checkout/fill_shipping.hbs'
import updatePaymentTemplate from 'templates/popup/checkout/payment.hbs'
import updatingPaymentTemplate from 'templates/popup/checkout/fetching_payment.hbs'
import savingOrderTemplate from 'templates/popup/checkout/order_saving.hbs'
import addCreditCardTemplate from 'templates/popup/checkout/add_credit_card_information.hbs'
import confirmAndProceedTemplate from 'templates/popup/checkout/confirm_and_proceed.hbs'
import orderConfirmedTemplate from 'templates/popup/checkout/confirmed.hbs'
import progressTemplate from 'templates/popup/sync/progress.hbs'
import syncSuccess from 'templates/popup/sync/sync-success.hbs'

import { IMPORT_STATE_UPDATED, POPUP_SELECTOR, POPUP_DURATION } from 'const'
import { bindEvents, getStoreNameByUrl } from 'utils'
import { getImportErrorFromResponseError, handleImportButtonClick } from './utils'

export default class Popup {
  constructor () {
    this.timeout = POPUP_DURATION

    handleImportButtonClick()
    $('.modalyst-extension-popup').on('mouseover', debounce(this.setVisibilityTimeout.bind(this), 100))

    bindEvents({
      [IMPORT_STATE_UPDATED]: ({ state }) => this.render(state)
    })
  }

  render (state) {
    const newContent = this.content(state)
    this.forceVisible = this.shouldBeVisible(state)
    if (newContent) {
      this.setContent(newContent)
      this.toggleVisibility(true)
      this.setVisibilityTimeout()
    }
  }

  setVisibilityTimeout () {
    if (this.visibilityTimeout) {
      clearTimeout(this.visibilityTimeout)
    } else {
      this.toggleVisibility(true)
    }
    if (this.forceVisible) return
    this.visibilityTimeout = setTimeout(() => {
      if (this.forceVisible) return
      this.visibilityTimeout = null
      this.toggleVisibility(false)
    }, this.timeout)
    // go back to default timeout value
    this.timeout = POPUP_DURATION
  }

  content (state) {
    if (state.error) {
      this.setVisibilityTimeout()
      return this.errorContent(state.error)
    }

    if (state.confirmAndProceed) {
      return confirmAndProceedTemplate()
    }

    switch (state.checkoutStep) {
      case 'init':
      case 'loading_urls':
      case 'adding_products_to_bag':
      case 'start_checkout':
        return updatingCartTemplate()
      case 'shipping':
      case 'fill_shipping_information':
        return state.missingInformation ? updateShippingTemplate() : updatingShippingTemplate()
      case 'order_finished':
      case 'confirming':
        return savingOrderTemplate()
      case 'add_credit_card':
        return addCreditCardTemplate()
      case 'confirmed':
      case 'order_payed':
        this.setVisibilityTimeout()
        return orderConfirmedTemplate({ external_store: getStoreNameByUrl(location.host) })
      case 'payment':
        if (document.location.pathname.startsWith('/shipping')) return updateShippingTemplate()
        if (state.noPaymentMethod) return updatePaymentTemplate()
        return updatingPaymentTemplate()
    }

    switch (state.sync) {
      case 'start':
        return progressTemplate({ text: 'Syncing in progress' })
      case 'finished':
        this.setVisibilityTimeout()
        return syncSuccess()
    }

    if (/^\/(shipping|payment|order)/.test(document.location.pathname)) return // Inside checkout

    if (state.progress.length) return defaultPopupTemplate({ status: 'waiting', title: 'Importing...' })
    if (state.lastImportedProductId) return defaultPopupTemplate({ status: 'success', title: 'Added to Modalyst' })
  }

  shouldBeVisible (state) {
    return !!(
      state.checkoutStep ||
      state.progress.length ||
      (state.sync && state.sync !== 'finished')
    )
  }

  errorContent (error) {
    if (error.type === 'import') {
      const { title, message } = getImportErrorFromResponseError(error)
      return defaultPopupTemplate({ status: 'error', title, message })
    }

    if (error.type === 'checkout') {
      return defaultPopupTemplate({ status: 'error', title: 'Error!' })
    }

    if (error.type === 'sync') {
      return defaultPopupTemplate({ status: 'error', title: 'Error!' })
    }
  }

  setContent (content) {
    $(POPUP_SELECTOR).html(content)
  }

  toggleVisibility (visible) {
    $(POPUP_SELECTOR).toggleClass('visible', visible)
  }
}
