import $ from 'jquery'

import { publish } from 'utils'
import { START_PRODUCT_IMPORT } from 'const'
import popupTemplate from 'templates/popup.hbs'
import { ERRORS } from './const'

/**
 *
 * @param error
 * @returns {{title: string, message: string}}
 */
const getImportErrorFromResponseError = (error) => {
  let title, message

  if (error.error === ERRORS.CONFLICT) title = 'Already Imported to Modalyst!'
  else if (error.error === ERRORS.LIMIT_EXCEEDED) {
    title = 'Product Limit Reached!'
    message = 'You have added the maximum number of products in your plan. To add more products, upgrade your plan'
  } else {
    title = 'Import failed!'
    if (error.error) message = error.error.message
  }

  return {
    title,
    message
  }
}

/**
 * Automatically publish START_PRODUCT_IMPORT event if an element with data-modalyst-import was clicked.
 */
function handleImportButtonClick () {
  $('body')
    .append(popupTemplate())
    .on('click', '[data-modalyst-import]', function () {
      publish(START_PRODUCT_IMPORT, $(this).attr('data-product-id'))
    })
}

export {
  getImportErrorFromResponseError,
  handleImportButtonClick
}
