import 'stores/aliexpress.scss'

import setupCommunication from './injected_communication'
import ButtonIntegration from './button_integration'
import AliexpressPopup from './popup'
import AliexpressState from './state'
import setupContentScriptCommon from 'content_script_core'
import setupAliexpressAnalytics from './analytics'
import { performInjection } from 'utils';

(function aliexpressSetup () {
  setupContentScriptCommon('aliexpress')
  performInjection('src/aliexpress_injected.js')

  const state = new AliexpressState()
  new AliexpressPopup() // eslint-disable-line no-new

  setupCommunication()
  setupAliexpressAnalytics()

  new ButtonIntegration(state) // eslint-disable-line no-new
})()
