import { BACKGROUND_EVENTS, PRODUCT_IMPORT_ERROR, PRODUCT_IMPORT_SUCCEEDED } from 'const'
import { publish } from 'utils'

const importProduct = (productId) => {
  chrome.runtime.sendMessage({
    topic: BACKGROUND_EVENTS.IMPORT_ITEM,
    item: { id: productId },
    store: 'aliexpress'
  }, (response) => {
    if (response.status === PRODUCT_IMPORT_SUCCEEDED) {
      publish(PRODUCT_IMPORT_SUCCEEDED, response.id)
    } else {
      publish(PRODUCT_IMPORT_ERROR, { id: response.id, error: response.error })
    }
  })
}

export {
  importProduct
}
