import { bindEvents } from 'utils'
import { IMPORT_STATE_UPDATED } from 'const'
import $ from 'jquery'

let userClicked = false

const ENTER_KEY = 13

function shouldBlock (state) {
  return !!(
    state.progress.length ||
    (state.checkoutStep &&
      !['confirmed', 'order_payed', 'order_finished'].includes(state.checkoutStep)) ||
    userClicked
  )
}

function removeDialog () {
  userClicked = true
  $('body').removeClass('blockunload')
}

$(document).on(
  'click',
  '#submit-form-btn, .checkout-step a, #paypal-option, .j-cart-check, .j-place-order, .check-btn, .j-sign-in button, div[class^=CartOrderSummary], div[class^=CheckoutPage__NextButton], div[class^=CheckoutPage__PaypalButton], div[class^=CheckoutPage__PlaceOrder], div[class^=CheckoutPage__Header]',
  () => {
    removeDialog()
  }
)

// handle login submit with 'Enter'
$(document).on('keypress', '.j-sign-in input[type=password]', e => {
  if (String(e.keyCode) === String(ENTER_KEY) || String(e.which) === String(ENTER_KEY)) {
    removeDialog()
  }
})

export default function blockerSetup () {
  bindEvents({
    [IMPORT_STATE_UPDATED]: ({ state }) => $('body').toggleClass('blockunload', shouldBlock(state))
  })
}
