/*
 * Manages store content script's state
 */
import mapValues from 'lodash/mapValues'
import pull from 'lodash/pull'
import { publish, bindEvents } from 'utils'
import {
  AUTH_SUCCESS,
  AUTH_FAILURE,
  START_PRODUCT_IMPORT,
  PRODUCT_IMPORT_SUCCEEDED,
  IMPORT_STATE_UPDATED,
  CHECKOUT_STEP_STARTED,
  CHECKOUT_ERROR,
  PRODUCT_IMPORT_ERROR,
  START_SYNC,
  SYNC_SUCCESS,
  SYNC_ERROR,
  ENTER_SHIPPING_INFORMATION,
  CONFIRM_AND_PROCEED
} from 'const'

export default class State {
  constructor () {
    this.state = {
      progress: []
    }
    this.bindEvents()
  }

  get events () {
    return {
      [AUTH_SUCCESS]: 'authSuccess',
      [AUTH_FAILURE]: 'authFailed',
      [START_PRODUCT_IMPORT]: 'importStarted',
      [PRODUCT_IMPORT_SUCCEEDED]: 'importFinished',
      [PRODUCT_IMPORT_ERROR]: 'importFailed',
      [CHECKOUT_STEP_STARTED]: 'updatingCart',
      [CHECKOUT_ERROR]: 'checkoutError',
      [START_SYNC]: 'startSync',
      [SYNC_SUCCESS]: 'finishSync',
      [SYNC_ERROR]: 'syncError',
      [ENTER_SHIPPING_INFORMATION]: 'enterShippingInformation',
      [CONFIRM_AND_PROCEED]: 'confirmAndProceed'
    }
  }

  wrappedEvents () {
    const wrapper = eventName => data => {
      this.clearErrors()
      this[this.events[eventName]](data)
      publish(IMPORT_STATE_UPDATED, { eventName, state: this.state })
    }

    return mapValues(this.events, (_localCb, eventName) => wrapper(eventName))
  }

  bindEvents () {
    bindEvents(this.wrappedEvents())
  }

  clearErrors () {
    delete this.state.error
  }

  authSuccess ({ username }) {
    this.state.authenticated = true
    this.state.username = username
  }

  authFailed () {
    this.state.authenticated = false
  }

  importStarted (productId) {
    this.state.progress.push(productId)
  }

  updatingCart (step) {
    this.state.confirmAndProceed = false
    this.state.checkoutStep = step
  }

  checkoutError (data) {
    this.state.error = { type: 'checkout' }
    if (data.reason) {
      this.state.error.reason = data.reason
    }
  }

  importFinished (productId) {
    pull(this.state.progress, productId)
    this.state.lastImportedProductId = productId
  }

  importFailed ({ id, error }) {
    pull(this.state.progress, id)
    this.state.error = { type: 'import', productId: id, error }
  }

  startSync () {
    this.state.sync = 'start'
  }

  syncError () {
    this.state.error = {
      type: 'sync'
    }
  }

  finishSync () {
    this.state.sync = 'finished'
  }

  noPaymentMethod () {
    this.state.noPaymentMethod = true
  }

  enterShippingInformation () {
    this.state.missingInformation = true
  }

  confirmAndProceed () {
    this.state.confirmAndProceed = true
  }
}
