import { INTERNAL_EVENT_IN_NAME, INTERNAL_EVENT_OUT_NAME } from 'const'
import { publish } from './index'

/*
 * Sends message to the injected script
 */
export function sendMessage (topic, data) {
  setTimeout(
    () =>
      document.dispatchEvent(
        new CustomEvent(INTERNAL_EVENT_IN_NAME, {
          detail: { topic: topic, data: data }
        })
      ),
    0
  )
}

/*
 * Listen for messages from the injected script and translates them to messages in "injected" namespace
 */
export function setListener () {
  document.addEventListener(INTERNAL_EVENT_OUT_NAME, e => {
    publish(`injected.${e.detail.topic}`, e.detail.data)
  })
}
