import $ from 'jquery'
import buttonTemplate from 'templates/button.hbs'
import { BUTTON_TEXT_FOR_STATUS, ALL_IMPORT_BUTTON_STATUSES } from 'const'
import { publish } from 'utils'
import { CLASSES, PRODUCT_LIST_CHANGED, QUERY_SELECTORS } from '../const'
import { extractProductId } from './button_integration'

const isProductListPage = () => {
  const viewType = '' + $('body').data('spm')
  return viewType === 'productlist' || viewType.startsWith('search')
}

const isProductDetailPage = () => {
  const viewType = '' + $('body').data('spm')
  return viewType === 'detail'
}

/**
 * Removes all buttons from the list/grid
 */
const removeAllButtons = () => {
  $('.modalyst-button-wrapper').remove()
  $('.' + CLASSES.MODALYST_BUTTON_INJECTED).removeClass(CLASSES.MODALYST_BUTTON_INJECTED)
}

/**
 * Injects Modalyst button on product detail page.
 */
const addButtonToProductPage = (productId = null) => {
  const productAction = $('.product-action')
  if (productAction.hasClass(CLASSES.MODALYST_BUTTON_INJECTED)) return

  productAction.addClass(CLASSES.MODALYST_BUTTON_INJECTED)
  productAction.before(buttonTemplate({
    klass: CLASSES.MODALYST_BUTTON_LARGE,
    productId
  }))
}

/**
 * Injects Modalyst buttons to items on list view.
 * @param element jQuery NodeList
 * @param productId
 */
const addButtonsToListItems = (element, productId = null) => {
  element
    .find(QUERY_SELECTORS.PRODUCT_CARD_BUTTON_CONTAINER)
    .append($(buttonTemplate({ productId })))
}

/**
 * Iterate over product cards and inject Modalyst button, unless already injected.
 */
const addButtonsToItems = () => {
  $(QUERY_SELECTORS.PRODUCT_CARD).each(function () {
    if ($(this).hasClass(CLASSES.MODALYST_BUTTON_INJECTED)) return
    $(this).addClass(CLASSES.MODALYST_BUTTON_INJECTED)
    const productUrl = $(this).attr('href')
    const productId = extractProductId(productUrl)
    addButtonsToListItems($(this), productId)
    $('.' + CLASSES.MODALYST_INNER_LINK).click(function(e) {
      e.preventDefault()
    })
  })
}

/**
 * Updates product button, usually after import is successful or failed.
 * @param {number} productId To locate the button for the product.
 * @param {string} status A status to which the buttons should be changed.
 * @param {boolean} disable Whether to disable the button as part of this update.
 */
function updateProductButton (productId, status, disable = false) {
  const button = $(QUERY_SELECTORS.EXACT_MODALYST_BUTTON(productId))

  // Exit early if button already has the intended status.
  // This is to prevent loops in mutation observers that would update buttons
  // endlessly.
  if (button.hasClass(status)) return

  button.removeClass(ALL_IMPORT_BUTTON_STATUSES) // remove any previous statuses
  button.addClass(status) // add new status
  button.find('.modalyst-button__text').text(BUTTON_TEXT_FOR_STATUS.get(status))

  if (disable) {
    button.removeData('data-modalyst-import')
    button.removeAttr('data-modalyst-import')
    button.prop('disabled', true)
  }
}

/**
 * Adds observers that are required to inject/change buttons after specific changes on the page.
 * @returns {{paginationChangeObserver: MutationObserver, itemsDynamicInjectObserver: MutationObserver}}
 */
function addObservers () {
  // Dynamic items injector Observer
  const itemsDynamicInjectObserver = new MutationObserver(() => {
    publish(PRODUCT_LIST_CHANGED, { PRODUCT_LIST_CHANGED })
  })
  itemsDynamicInjectObserver.observe(document.querySelector(QUERY_SELECTORS.PRODUCT_CARD_CONTAINER), {
    childList: true,
    attributes: true,
    attributeFilter: ['class']
  })

  // Pagination Observer
  const paginationChangeObserver = new MutationObserver((mutationList) => {
    publish(PRODUCT_LIST_CHANGED, { PRODUCT_LIST_CHANGED })
  })
  paginationChangeObserver.observe(document.querySelector('.list-pagination'), {
    childList: true,
    subtree: true,
    attributes: true
  })

  return {
    itemsDynamicInjectObserver,
    paginationChangeObserver
  }
}

export {
  addButtonsToItems,
  addButtonToProductPage,
  isProductDetailPage,
  isProductListPage,
  updateProductButton,
  removeAllButtons,
  addObservers
}
