import $ from 'jquery'

import { AUTH_FAILURE, AUTH_SUCCESS, IMPORT_STATE_UPDATED, IMPORT_BUTTON_STATUSES } from 'const'
import { bindEvents } from 'utils'
import { PRODUCT_LIST_CHANGED } from '../const'
import {
  addButtonsToItems,
  addButtonToProductPage,
  isProductDetailPage,
  isProductListPage,
  updateProductButton,
  addObservers,
  removeAllButtons
} from './utils'

/**
 * Extracts product id from the URL of the product.
 */
export function extractProductId (href) {
  return href
    .replace(/^.*[/]/, '')
    .replace(/\..*/, '')
}

/**
 * Injects `Add to Modalyst` buttons to all products on product list,
 * and also handles re-injecting if DOM subtree or attributes of list element change
 * (for example when AliExpress injects more items or when navigating to the next page).
 */
function injectProductListButtons () {
  if (!isProductListPage()) return
  addButtonsToItems()
  addObservers()
}

/**
 * Injects a single `Add to Modalyst` button to product detail page.
 */
function injectProductPageButton () {
  if (!isProductDetailPage()) return

  const productId = extractProductId(window.location.pathname)
  addButtonToProductPage(productId)
}

/**
 * Injects `Add to Modalyst` buttons.
 */
function injectButtons () {
  injectProductListButtons()
  injectProductPageButton()
}

/**
 * Toggle `modalyst-buttons-enabled` on body and inject buttons if enabled.
 * @param {boolean} enabled
 */
const toggleButtons = (enabled) => {
  $('body').toggleClass('modalyst-buttons-enabled', enabled)
  if (enabled) injectButtons()
}

function setupButton () {
  toggleButtons(false)

  bindEvents({
    [AUTH_SUCCESS]: toggleButtons.bind(null, true),
    [AUTH_FAILURE]: toggleButtons.bind(null, false)
  })
}

export default class ButtonIntegration {
  constructor (state) {
    setupButton()

    this.state = state

    bindEvents({
      [IMPORT_STATE_UPDATED]: ({ state }) => {
        this.state = state
        this.updateButtonsFromState(state)
      },
      [PRODUCT_LIST_CHANGED]: () => {
        removeAllButtons()
        addButtonsToItems()
        this.updateButtonsFromState(this.state)
      }
    })
  }

  updateButtonsFromState ({
    imported = [],
    failed = [],
    progress = [],
    previouslyImported = []
  }) {
    imported.forEach(productId => {
      updateProductButton(productId, IMPORT_BUTTON_STATUSES.IMPORTED, true)
    })
    failed.forEach(productId => {
      updateProductButton(productId, IMPORT_BUTTON_STATUSES.ERROR, false)
    })
    previouslyImported.forEach(productId => {
      updateProductButton(productId, IMPORT_BUTTON_STATUSES.PREVIOUSLY_IMPORTED, true)
    })
  }
}
